import React, { Fragment} from 'react';
import "./Cart.css";
import CartItemCard from "./CartItemCard";
import { useDispatch, useSelector } from 'react-redux';
import { addItemsToCart , removeItemsFromCart} from '../../actions/cartAction';
import {Typography} from "@material-ui/core";
import RemoveShoppingCartIcon from "@material-ui/icons/RemoveShoppingCart";
import {Link,useHistory} from "react-router-dom";


const Cart = () => {
    const dispatch=useDispatch();
    const{cartItems}=useSelector((state)=>state.cart);
    const history = useHistory();
    const increaseQuantity=(id,quantity,stock)=>{
        const newQty=quantity+1;
        if(stock<=quantity){
            return;
        }
        dispatch(addItemsToCart(id,newQty));

    };
    const decreaseQuantity=(id,quantity)=>{
        const newQty=quantity-1;
        if(1>=quantity){
            return;
        }
        dispatch(addItemsToCart(id,newQty));

    };

    const deleteCartItems=(id)=>{
        dispatch(removeItemsFromCart(id));
    };
    const checkoutHandler = () => {
        history.push("/login?redirect=shipping");
      };
    
  return (
    
    <Fragment>
        {cartItems.length===0?(
            <div className='emptyCart'>
                <RemoveShoppingCartIcon/>
                <Typography>No product in your cart</Typography>
                <Link to="/vegetables">View Products</Link>
            </div>
        ):(
            
    <Fragment>
        
         <div className="cartPage">
            <div className="cartHeader">
                <p>&emsp;Product</p>
                <p>Quantity</p>
                <p>Subtotal</p>
            </div>
            
           {cartItems && cartItems.map((item)=>(
                 <div className="cartContainer" key={item.product}>
                 <CartItemCard item={item} deleteCartItems={deleteCartItems}/>
                 <div className="cartInput">
                     <button onClick={()=>
                      decreaseQuantity(item.product,item.quantity)}>-</button>
                     <input type="number" value={item.quantity} readOnly/>
                     <button onClick={()=>
                        increaseQuantity(item.product,item.quantity,item.stock)}>+</button>
                 </div>
                 <p className="cartSubtotal">{`${item.price*item.quantity }`}</p>
                 </div>
           ))}
                
                
                <div className="cartGrossTotal">
                    <div></div>
                    <div className="cartGrossTotalBox">
                        <p style={{fontSize:"20px",color:"black"}}>Gross Total</p>
                        <p style={{fontSize:"20px",color:"black"}}>{`${cartItems.reduce((acc,item)=>acc+item.quantity*item.price,0)}`}</p>
                    </div>
                    <div></div>
                    <div className="checkOutBtn">
                        <button onClick={checkoutHandler}>
                            <p style={{fontSize:"22px",color:"black"}}>Check Out</p>
                        </button>
                    </div>
                </div>
         </div>
         
    </Fragment>
        )} 
        </Fragment>

  
  );
};


export default Cart;
